  font-weight: 300;
  text-transform: uppercase;
  border-radius: 100rem;
  border: none;
  background-image: linear-gradient(to right, var(--color-primary-light), var(--color-primary-dark));
  color: #fff;
  cursor: pointer;
  position: relative;
  outline: 2px solid transparent;
  outline-offset: -2px;
  transition: all 0.2s;
  overflow: hidden; }
  .btn > * {
